import api from './api'

export default {
    getResa() {
        return api.get('/rally');
    },
    createResa(resa) {
        return api.post('/rally/store/resa', resa);
    },
    updateResa(resa) {
        return api.post('/rally/update/resa', resa);
    }, 
    deleteResa(id) {
        return api.get('/rally/delete/resa/' + id);
    },
    addPilote(pilote) {
        return api.post('/rally/store/pilote', pilote);
    },
    updatePilote(pilote) {
        return api.post('/rally/update/pilote/admin', pilote);
    },
    deletePilote(id) {
        return api.get('/rally/delete/pilote/' + id);
    }
}