<template>
  <v-data-table
    :headers="headers"
    :items="resas"
    sort-by="dateRoulage"
    class="elevation-1"
    show-expand
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Suivi Date Rallye</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              New Date
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      :return-value.sync="editedItem.dateRoulage"
                      transition="scale-transition"
                      offset-y
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="editedItem.dateRoulage"
                          label="Choisir une date"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="editedItem.dateRoulage"
                        no-title
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn text color="primary" @click="menu = false">
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="$refs.menu.save(editedItem.dateRoulage)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-menu>
                  </v-col>

                  <v-col cols="12">
                    <v-select
                      v-model="editedItem.matinOuSoir"
                      :items="momentTab"
                      label="Selectionner un moment"
                    ></v-select>
                  </v-col>

                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.nbrPilote"
                      label="Nbr Pilote"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5"
              >Are you sure you want to delete this item?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >OK</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length">
        <Pilote :pilotes="item" />
      </td>
    </template>

    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>

    <template v-slot:item.dateRoulage="{ item }">
      {{ changeViewDate(item.dateRoulage) }}
    </template>

    <template v-slot:item.matinOuSoir="{ item }">
      <v-chip :color="getColor(item.matinOuSoir)" dark>
        {{ item.matinOuSoir }}
      </v-chip>
    </template>

    <template v-slot:item.Pilotes="{ item }">
      {{ item.Pilotes.length }}
    </template>

    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize"> Reset </v-btn>
    </template>
  </v-data-table>
</template>

<script>
import apiRallye from "../services/rallye";
import Pilote from "../components/Pilote.vue";

export default {
  components: {
    Pilote,
  },
  data: () => ({
    date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    menu: false,
    modal: false,
    menu2: false,
    momentTab: ["matin", "aprem"],
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Id Journée",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "Date Roulage", value: "dateRoulage" },
      { text: "Moment", value: "matinOuSoir" },
      { text: "Nbr Pilote Max", value: "nbrPilote" },
      { text: "Nbr Pilote Inscrit", value: "Pilotes" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    resas: [],
    editedIndex: -1,
    editedItem: {
      _id: "",
      dateRoulage: "",
      matinOuSoir: "",
      nbrPilote: 0,
      Pilotes: [],
      id: "",
    },
    defaultItem: {
      _id: "",
      dateRoulage: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      matinOuSoir: "",
      nbrPilote: 0,
      Pilotes: [],
      id: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Création Date Roulage"
        : "Modification résa";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    getColor(moment) {
      if (moment == "matin") {
        return "second";
      } else {
        return "accent";
      }
    },
    changeViewDate(date) {
      date = new Date(date);
      var tab_jour = new Array(
        "Dimanche",
        "Lundi",
        "Mardi",
        "Mercredi",
        "Jeudi",
        "Vendredi",
        "Samedi"
      );

      return tab_jour[date.getDay()] + "-" + date.toLocaleDateString("fr");
    },
    initialize() {
      fetch("https://mytaskcfg.herokuapp.com/api/rally")
        .then((res) => res.json())
        .then((res) => {
          this.resas = res;
        })
        .catch((error) => alert("Erreur : " + error));
    },

    editItem(item) {
      this.editedIndex = this.resas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.resas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      var res = await apiRallye.deleteResa(this.editedItem.id);
      console.log(res);
      this.resas.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      var resa = {
        date: this.editedItem.dateRoulage,
        moment: this.editedItem.matinOuSoir,
        nbrPilote: this.editedItem.nbrPilote,
      };

      if (this.editedIndex > -1) {
        resa.id = this.editedItem.id;
        var res = await apiRallye.updateResa(resa);
        console.log(res);
        Object.assign(this.resas[this.editedIndex], this.editedItem);
      } else {
        var res = await apiRallye.createResa(resa);
        res.data.Pilotes = [];
        this.resas.push(res.data);
      }
      this.close();
    },
  },
};
</script>
