<template>
  <v-data-table :headers="headers" :items="pilotes.Pilotes" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Liste des pilotes</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              New pilote
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
              <v-spacer></v-spacer>
              <v-switch
                v-model="editedItem.paiement"
                :label="`Paiement: ${editedItem.paiement.toString()}`"
              ></v-switch>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.name"
                      label="Nom Prénom"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.voiture"
                      label="Voiture"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-select
                      :items="formules"
                      v-model="editedItem.formule"
                      @change="changeFormule()"
                      label="Formule"
                    ></v-select>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="editedItem.phone"
                      label="Tel"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5"
              >Are you sure you want to delete this item?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >OK</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>

    <template v-slot:item.nbrSession="{ item }">
      <v-chip :color="getColorNbrSession(item.nbrSession)" dark>
        {{ item.nbrSession }}
      </v-chip>
    </template>

    <template v-slot:item.paiement="{ item }">
      <v-chip :color="getColorPaiement(item.paiement)" dark>
        {{ item.paiement }}
      </v-chip>
    </template>
  </v-data-table>
</template>
<script>
import apiPilote from "../services/rallye";
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Résa N°",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "Nom pilote", value: "name" },
      { text: "Voiture", value: "voiture" },
      { text: "Formule", value: "formule" },
      { text: "Contact", value: "phone" },
      { text: "Nbr Session", value: "nbrSession" },
      { text: "Paiement", value: "paiement" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    editedIndex: -1,
    editedItem: {
      name: "",
      voiture: "",
      formule: "",
      phone: "",
      nbrSession: 0,
      paiement: false,
      rally: "",
    },
    defaultItem: {
      name: "",
      voiture: "",
      formule: "",
      phone: "",
      nbrSession: 0,
      paiement: false,
      rally: "",
    },
    formules: [
      "1 session 10min => 25€",
      "3 sessions 10min => 60€",
      "1/2 journée => 180€",
      "1 journée => 250€",
    ],
  }),
  props: ["pilotes"],

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Ajout d'un Pilote"
        : "Modifier un Pilote";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    getPaiement(boolpay) {
      if (boolpay) {
        return "payé";
      } else {
        return "non payé";
      }
    },
    getColorPaiement(boolpay) {
      if (boolpay) {
        return "green";
      } else {
        return "error";
      }
    },
    getColorNbrSession(nbr) {
      if (nbr == 1) {
        return "accent";
      } else if (nbr <= 0) {
        return "error";
      } else {
        return "green";
      }
    },
    initRally() {
      this.editedItem.rally = this.pilotes.id;
    },
    changeFormule() {
      if (this.editedItem.formule == "1 session 10min => 25€") {
        this.editedItem.nbrSession = 1;
      } else if (this.editedItem.formule == "3 sessions 10min => 60€") {
        this.editedItem.nbrSession = 3;
      } else {
        this.editedItem.nbrSession = 99;
      }
    },
    editItem(item) {
      this.editedIndex = this.pilotes.Pilotes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.pilotes.Pilotes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      var res = await apiPilote.deletePilote(this.editedItem.id);
      console.log(res);
      this.pilotes.Pilotes.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      if (this.editedIndex > -1) {
        var res = await apiPilote.updatePilote(this.editedItem);
        Object.assign(this.pilotes.Pilotes[this.editedIndex], this.editedItem);
      } else {
        this.initRally();
        var res = await apiPilote.addPilote(this.editedItem);
        this.pilotes.Pilotes.push(res.data);
      }

      console.log(res);
      this.close();
    },
  },
};
</script>